* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.countdown-title {
  color: #396;
  margin: 40px 230px 20px;
  font-size: 40px;
  font-weight: 100;
}

.timer {
  color: #fff;
  text-align: center;
  font-family: sans-serif;
  font-size: 30px;
  font-weight: 100;
  display: inline-block;
}

.field {
  background: #00bf96;
  border-radius: 3px;
  padding: 10px;
  display: inline-block;
}

.value {
  background: #00816a;
  border-radius: 3px;
  padding: 15px;
  display: inline-block;
}

.label {
  padding-top: 5px;
  font-size: 16px;
  display: block;
}

